body{
    background:rgb(16, 8, 59);
}
#overlay
{
    width:100%;
    height: 100vh;
    background:linear-gradient(45deg , rgb(40, 40, 179),rgb(0, 0, 0),rgb(185, 10, 176));
    display:flex;
    align-items:center;
    justify-content:center;
    clip-path:circle( 200px at var(--x) var(--y));
    /* transition:1s; */
}
#overlay h1 {
    font-size:10rem;
    text-shadow:5px 5px rgb(44, 42, 42);
    font-family: 'Tourney', cursive;
    color:white;
}
#overlay h1:hover 
{
    text-shadow:5px 5px rgb(0, 0, 0);

}
.center
{
    display:grid;
    place-items:center;
}
.center button{
    width:26rem;
    height:5rem;
    font-family: 'Orbitron', sans-serif;
    font-size:2rem;
    background:linear-gradient(45deg , rgb(35, 35, 148),rgb(255, 255, 255),rgb(231, 62, 223));
    font-weight:500;
    filter:drop-shadow(3px 3px grey);
    cursor:pointer;
}
.center button:hover{
    letter-spacing:3px;
    width:27rem;
    filter:drop-shadow(10px 10px 5px grey);
    font-weight:900;
    /* word-spacing:.2rem; */
}