*
{
    margin:0;
    padding:0;
    scroll-behavior: smooth;
    --darkblue:#0c122f;
    --skyblue:#00ccff;
    --darkpink:#d400d4;
    --heading:grey;
    
}

.main 
{
    width:100%;
    height: 100vh;
    background:#0c122f;
    transition:.5s;
    display:flex;
    justify-content:center;
    align-items:center;

}

.main-div
{
    width:80%;
    height:85%;
    background:rgb(0, 0, 0);
    border-radius : 40px;
    position:relative;
   
    
}
.main-div:before 
{   content:"Hi";
    position:absolute;
    left:-100px;
    top:100px;
    font-size:5rem;
    font-weight:700;
    color:var(--darkblue);
    text-shadow:0px 0px 5px white;
    letter-spacing:5px;
  
}
.main-div:after
{ 
 content: "";
 position:absolute;
 top:-5px;
 left:-1px;
 bottom:-5px;
 right:-1px;
 z-index: 5;
 background:linear-gradient(315deg,var(--skyblue),var(--darkblue),var(--darkpink) );
 border-radius:50px;
 -webkit-transform:skewX(.5deg) skewY(.8deg);
         transform:skewX(.5deg) skewY(.8deg);
}

.main-content
{
    width:100%;
    height:100%;
    background:linear-gradient(315deg ,rgb(13, 7, 44),black,rgb(15, 7, 61) );
    z-index:10;
    position:absolute;
    top:0;
    left:0;
    bottom:0;
    right:0;
    border-radius:55px;
 
}
.main-content a
{
  font-size:2rem;
  position:absolute;
  left:2rem;
  text-decoration:none;
  list-style:none;
  top:1rem;
  color:var(--skyblue);
}
.main-content h1 
{
  color:white;
  z-index:2;
  font-family: 'Tourney', cursive; 
  font-size:4.5rem;
  position:absolute;
  top:6.8rem;
  font-weight:100;
}
.main-content h1:hover 
{
  font-weight:700;
}
.main-content p
{
  margin-top:2rem;
  color:rgb(92, 17, 121);
  position:absolute;
  top:13rem;
  right:1rem;
  font-size:1.5rem;
  word-spacing:1rem;
  font-weight:100;
  padding:1rem;
  line-height:2rem;
}
.main-content .div 
{
  color:white;
  width:4rem;
  height:4rem;
  z-index:5;
  font-size:2rem;
}


.main-btn 
{
  width:16rem;
  height:5rem;
  background:var(--skyblue);
  position:absolute;
  bottom:10rem;
  right:23rem;
  z-index: 20;
  cursor:pointer;
  color:black;
  border-radius:50px;
  text-decoration:none;
  display:grid;
  place-items:center;
  font-size:1.3rem;
  font-weight:500;
}
.main-btn:hover{
  letter-spacing:1px;
  /* transform:skewX(6deg); */
  box-shadow: 2px 10px 2px rgb(14, 83, 95);
  -webkit-transform:translateY(-5px);
          transform:translateY(-5px);

}
.main-image
{
  width:30rem;
  height:20rem;
  background:url(/myprofile/static/media/main.8262a2da.png);
  background-position:center;
  background-size:cover;
  position:absolute;
  z-index: 70;
  bottom:0;
  left: 10rem;
}


/* responsive  */

@media only screen and (max-width:1550px)  {
  .main-content p{
    font-size:1.4rem;
    word-spacing:5px;
  }
  .main-content h1 {
    font-size:4rem;
  }
  .main-image {
    width:20rem;
    height:15rem;
    position:absolute;
   left:1rem;
   bottom:0;
  }
  .main-btn {
    right:1rem;
    bottom:5rem;
    width:10rem;
    height:4rem;
    text-align:center;
  }
}
@media only screen and (max-width:600px){
  .main {
    height:42rem;
  }
  .main-div{
    height:40rem;
   
  }
  .main-content{
    height:40rem;
  }
  .main-div:after{
    height:40rem;
  }
  .main-div:before {
    font-size:2rem;
    left:-2.5rem;
    z-index:200;
    top:3rem;
  }
  .main-content a{
     font-size:1rem;
  }
  .main-content h1{
    top:3rem;
    font-size:1.8rem;
    word-spacing:2px;
    margin-bottom:.3rem;
 }
  .main-content p{
    font-size:1rem;
    word-spacing:2px;
    margin-top:0;
    line-height:22px;
    top:7.5rem;
 }
 .main-image {
  width:13rem;
  height:10rem;
  position:absolute;
 left:4rem;
 bottom:4rem;
}
.main-btn 
{
  width:5rem;
  height:2rem;
  bottom:1.5rem;
  right:5rem;
  overflow:hidden;
  font-size:.6rem;
  text-align:center;
  font-weight:400;
}
}

